/**
 * Sign-up (customer registration) hook
 * @module features/auth/hooks/use-signup
 */

import { useState } from 'react'
import type { SignUpInput } from '../types/auth.types'

export interface UseSignUpOptions {
  onSuccess?: (email: string) => void
  onError?: (error: string) => void
}

export interface UseSignUpReturn {
  signUp: (data: SignUpInput, phoneVerified: boolean, emailVerified: boolean) => Promise<void>
  isLoading: boolean
  error: string
  success: string
  clearMessages: () => void
}

/**
 * Hook for handling customer sign-up
 *
 * @example
 * ```tsx
 * const { signUp, isLoading, error, success } = useSignUp({
 *   onSuccess: (email) => {
 *     setShowSuccessModal(true);
 *     setUserEmail(email);
 *   },
 * });
 *
 * await signUp(formData, phoneVerified, emailVerified);
 * ```
 */
export function useSignUp(options: UseSignUpOptions = {}): UseSignUpReturn {
  const { onSuccess, onError } = options
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  const clearMessages = () => {
    setError('')
    setSuccess('')
  }

  const signUp = async (data: SignUpInput, phoneVerified: boolean, emailVerified: boolean) => {
    setIsLoading(true)
    setError('')
    setSuccess('')

    // Validation checks
    if (!phoneVerified) {
      const errorMessage = 'íœ´ëŒ€í° ì¸ì¦ì„ ì™„ë£Œí•´ì£¼ì„¸ìš”.'
      setError(errorMessage)
      onError?.(errorMessage)
      setIsLoading(false)
      return
    }

    if (!emailVerified) {
      const errorMessage = 'ì´ë©”ì¼ ì¸ì¦ì„ ì™„ë£Œí•´ì£¼ì„¸ìš”.'
      setError(errorMessage)
      onError?.(errorMessage)
      setIsLoading(false)
      return
    }

    try {
      const response = await fetch('/api/auth/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...data,
          role: 'CUSTOMER',
          phoneVerified,
        }),
      })

      if (response.ok) {
        const successMessage = 'íšŒì›ê°€ì…ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤!'
        setSuccess(successMessage)
        onSuccess?.(data.email)
      } else {
        const result = await response.json()

        let errorMessage = 'íšŒì›ê°€ì… ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.'
        if (result.code === 'EMAIL_ALREADY_EXISTS') {
          errorMessage = 'ì´ë¯¸ ê°€ì…ëœ ì´ë©”ì¼ì…ë‹ˆë‹¤.'
        } else if (result.message) {
          errorMessage = result.message
        }

        setError(errorMessage)
        onError?.(errorMessage)
      }
    } catch (err) {
      const errorMessage = 'íšŒì›ê°€ì… ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.'
      setError(errorMessage)
      onError?.(errorMessage)
      console.error('Sign-up error:', err)
    } finally {
      setIsLoading(false)
    }
  }

  return {
    signUp,
    isLoading,
    error,
    success,
    clearMessages,
  }
}
